
import { defineComponent } from 'vue'
import VueDatePicker from '@vuepic/vue-datepicker';
import PhoneInput from '@/shared/components/PhoneInput/PhoneInput.vue';
import baseTab from "@/shared/components/Tab/BaseTab.vue";
import basePan from "@/shared/components/Tab/BaseTabPan.vue";
import { FileSize } from "@/shared/services/FileSizeService";
import { ICM21400121_REQ_BODY, ICM21400121_RES, } from '@/shared/type/ICM21400121.type';
import { ICM21400112_REQ_BODY, ICM21400112_RES, BRANCH_INFO, ATTACH_INFO_LIST, CONTENT_LIST, TimeObject, MODIFY_CARD } from '@/shared/type/ICM21400112.type';
import { ICM21400131_REQ_BODY, ICM21400131_RES } from '@/shared/type/ICM21400131.type';
import { ICM21400141_REQ_BODY, ICM21400141_RES } from '@/shared/type/ICM21400141.type';
import '@vuepic/vue-datepicker/dist/main.css'
import { modalController } from "@ionic/vue";
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue";
import { screenMode } from "@/shared/type/Approval.type"
import { APM20200113_RES } from "@/shared/type/APM20200113.type"
import { FILE_TYPE } from "@/shared/constants/common.const";
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';

const fileSize = new FileSize();

export default defineComponent({
      name: 'icm214001sc01',
      components: {
            baseTab,
            basePan,
            VueDatePicker,
            BreadCrumb,
            PhoneInput,
            ModifyCard,
            SelectOption
      },
      data() {
            const viewType = this.$route.query.type;

            const branchInfo = {
                  branchCode: "",
                  branchFax: "",
                  branchLatitude: "",
                  branchLongitude: "",
                  branchTel: "",
                  provinceCode: "",
                  displayYN: "Y",
                  branchSaturdayEndHour: { "hours": 0, "minutes": 0, "seconds": 0 },
                  branchSaturdayStartHour: { "hours": 0, "minutes": 0, "seconds": 0 },
                  branchWeekdayEndHour: { "hours": 0, "minutes": 0, "seconds": 0 },
                  branchWeekdayStartHour: { "hours": 0, "minutes": 0, "seconds": 0 },
                  attachInfoList: [] as ATTACH_INFO_LIST[],
                  contentList: [] as CONTENT_LIST[],
                  statusCode: ""
            } as BRANCH_INFO;

            const fileInfo = {
                  image: "",
                  imgSize: "",
                  imageName: "",
            };

            const branchDate = {} as BRANCH_INFO;

            const headersIcon = [
                  { key: "En", name: "common.english" },
                  { key: "Km", name: "common.khmer" },
                  { key: "Ko", name: "common.korean" },
            ];

            const boardLangDefaultValue = [
                  { languageCode: "common.english", branchAddress: "", branchName: "", branchDescription: "" },
                  { languageCode: "common.khmer", branchAddress: "", branchName: "", branchDescription: "" },
                  { languageCode: "common.korean", branchAddress: "", branchName: "", branchDescription: "" },
            ];

            const modifyCard = {
                  registerTellerId: "",
                  registerTellerName: "",
                  registerDate: "",
                  registerTime: "",
                  updatedTellerId: "",
                  updatedTellerName: "",
                  updatedDate: "",
                  updatedTime: "",
                  deleteTellerId: "",
                  deleteTellerName: "",
                  deleteDate: "",
                  deleteTime: "",
            } as MODIFY_CARD;

            const isInvalid = false;
            const isEdit = true;

            return {
                  viewType,
                  branchInfo,
                  fileInfo,
                  uploadInput: "",
                  getImage: null,
                  headersIcon,
                  boardLangDefaultValue,
                  isInvalid,
                  isEdit,
                  modifyCard,
                  branchDate,
                  isInvalidLatitude: false,
                  isInvalidLongitude: false,
                  isInvalidPhoneNumber: false,
                  isImageEmty: false,
                  operationMode: 'normal' as screenMode,
                  approvalRes: {} as any,
            }
      },

      watch: {
            'branchInfo.branchLatitude'(newVal) {
                  if (this.$validate.validateLatitude(newVal)) {
                        this.isInvalidLatitude = true
                  } else {
                        this.isInvalidLatitude = false
                  }
            },
            'branchInfo.branchLongitude'(newVal) {
                  if (this.$validate.validateLongitude(newVal)) {
                        this.isInvalidLongitude = true
                  } else {
                        this.isInvalidLongitude = false
                  }
            },
      },

      computed: {
            formartSaturdayStartHour(): string {
                  return this.$datetime.formatVueTimePicker(this.branchInfo.branchSaturdayStartHour)
            },
            formartSaturdayEndHour(): string {
                  return this.$datetime.formatVueTimePicker(this.branchInfo.branchSaturdayEndHour)
            },
            formartWeekdayEndHour(): string {
                  return this.$datetime.formatVueTimePicker(this.branchInfo.branchWeekdayEndHour)
            },
            formartWeekdayStartHour(): string {
                  return this.$datetime.formatVueTimePicker(this.branchInfo.branchWeekdayStartHour)
            },
            createDate(): string {
                  return `${this.branchInfo.firstRegisterTellerName} ${this.branchInfo.firstRegisterTellerID} ${this.branchInfo.firstRegisterBranchCode}`;
            },
            updateDate(): string {
                  return `${this.branchInfo.lastChangeTellerName} ${this.branchInfo.lastChangeTellerID} ${this.branchInfo.lastChangeBranchCode}`;
            },
            deleteDate(): string {
                  return `${this.branchInfo.deleteTellerName} ${this.branchInfo.deleteTellerID} ${this.branchInfo.deleteBranchCode}`;
            },
            createTime(): string {
                  return `${this.branchInfo.firstRegisterTime} ${this.branchInfo.firstRegisterDate}`;
            },
            updateTime(): string {
                  return `${this.branchInfo.lastChangeTime} ${this.branchInfo.lastChangeDate}`;
            },
            deleteTime(): string {
                  return `${this.branchInfo.deleteTime} ${this.branchInfo.deleteDate}`;
            },
            queryRoute() {
                  return this.$route.query;
            },
            displayYN() {
                  return this.$store.state.CommonCodeModule.commonCode.YnType
            },
            approvalType(): string {
                  return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
            },
      },

      async created() {
            if (this.approvalType === '') {
                  if (this.viewType == 'edit') {
                        await this.inquiryBranchDetail();
                  } else {
                        this.isEdit = false;
                  }
            } else {
                  await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                        let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                        this.approvalRes = authDetail.body
                        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'

                        if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                              const req: ICM21400112_REQ_BODY = {
                                    branchCode: authDetailParsed.branchCode
                              }

                              const res = await this.$request.getRequest("ICM21400112", req, false) as ICM21400112_RES

                              const contentsList = this.convertCommonCode(res?.body.contentList);

                              const data = this.mapValue(res.body);

                              for (let i = 0; i < contentsList.length; i++) {
                                    this.boardLangDefaultValue.map((item) => {
                                          if (item.languageCode === contentsList[i].languageCode) {
                                                item.branchAddress = contentsList[i].branchAddress;
                                                item.branchName = contentsList[i].branchName;
                                                item.branchDescription = contentsList[i].branchDescription;
                                          }
                                    })
                              }

                              this.branchInfo.attachInfoList = res.body.attachInfoList.map((item: ATTACH_INFO_LIST) => {
                                    return {
                                          attachTypeCode: item.attachTypeCode,
                                          fileId: item.fileId,
                                          filePath: item.filePath
                                    }
                              })

                              this.branchInfo = {
                                    branchCode: res.body.branchCode,
                                    branchFax: res.body.branchFax,
                                    branchLatitude: res.body.branchLatitude,
                                    branchLongitude: res.body.branchLongitude,
                                    branchTel: res.body.branchTel,
                                    provinceCode: res.body.provinceCode,
                                    displayYN: res.body.displayYN,
                                    branchSaturdayEndHour: this.$datetime.convertTimeStringToTimeObject(data.branchSaturdayEndHour),
                                    branchSaturdayStartHour: this.$datetime.convertTimeStringToTimeObject(data.branchSaturdayStartHour),
                                    branchWeekdayEndHour: this.$datetime.convertTimeStringToTimeObject(data.branchWeekdayEndHour),
                                    branchWeekdayStartHour: this.$datetime.convertTimeStringToTimeObject(data.branchWeekdayStartHour),
                                    attachInfoList: res.body.attachInfoList,
                                    contentList: res.body.contentList,
                                    statusCode: res.body.statusCode
                              }

                        } else {
                              const contentsList = this.convertCommonCode(authDetailParsed.contentList);

                              const data = this.mapValue(authDetailParsed);

                              for (let i = 0; i < contentsList.length; i++) {
                                    this.boardLangDefaultValue.map((item) => {
                                          if (item.languageCode === contentsList[i].languageCode) {
                                                item.branchAddress = contentsList[i].branchAddress;
                                                item.branchName = contentsList[i].branchName;
                                                item.branchDescription = contentsList[i].branchDescription;
                                          }
                                    })
                              }

                              this.branchInfo.attachInfoList = authDetailParsed.attachInfoList.map((item: ATTACH_INFO_LIST) => {
                                    return {
                                          attachTypeCode: item.attachTypeCode,
                                          fileId: item.fileId,
                                          filePath: item.filePath
                                    }
                              })

                              this.branchInfo = {
                                    branchCode: authDetailParsed.branchCode,
                                    branchFax: authDetailParsed.branchFax,
                                    branchLatitude: authDetailParsed.branchLatitude,
                                    branchLongitude: authDetailParsed.branchLongitude,
                                    branchTel: authDetailParsed.branchTel,
                                    provinceCode: authDetailParsed.provinceCode,
                                    displayYN: authDetailParsed.displayYN,
                                    branchSaturdayEndHour: this.$datetime.convertTimeStringToTimeObject(data.branchSaturdayEndHour),
                                    branchSaturdayStartHour: this.$datetime.convertTimeStringToTimeObject(data.branchSaturdayStartHour),
                                    branchWeekdayEndHour: this.$datetime.convertTimeStringToTimeObject(data.branchWeekdayEndHour),
                                    branchWeekdayStartHour: this.$datetime.convertTimeStringToTimeObject(data.branchWeekdayStartHour),
                                    attachInfoList: authDetailParsed.attachInfoList,
                                    contentList: authDetailParsed.contentList,
                                    statusCode: authDetailParsed.statusCode
                              }
                        }
                  })
            }
      },

      methods: {
            async inquiryBranchDetail() {
                  const reqBody: ICM21400112_REQ_BODY = {
                        branchCode: String(this.$route.query.branchCode)
                  }

                  const res = await this.$request.getRequest('ICM21400112', reqBody, false) as ICM21400112_RES;

                  const data = this.mapValue(res.body);

                  this.branchDate = JSON.parse(JSON.stringify(res.body));

                  if (res.header.result) {
                        const contentsList = this.convertCommonCode(res.body.contentList);

                        for (let i = 0; i < contentsList.length; i++) {
                              this.boardLangDefaultValue.map((item) => {
                                    if (item.languageCode === contentsList[i].languageCode) {
                                          item.branchAddress = contentsList[i].branchAddress;
                                          item.branchName = contentsList[i].branchName;
                                          item.branchDescription = contentsList[i].branchDescription;
                                    }
                              })
                        }

                        this.branchInfo.attachInfoList = res.body.attachInfoList.map((item: ATTACH_INFO_LIST) => {
                              return {
                                    attachTypeCode: item.attachTypeCode,
                                    fileId: item.fileId,
                                    filePath: item.filePath
                              }
                        })

                        this.branchInfo = {
                              branchCode: res.body.branchCode,
                              branchFax: res.body.branchFax,
                              branchLatitude: res.body.branchLatitude,
                              branchLongitude: res.body.branchLongitude,
                              branchTel: res.body.branchTel,
                              provinceCode: res.body.provinceCode,
                              displayYN: res.body.displayYN,
                              branchSaturdayEndHour: this.$datetime.convertTimeStringToTimeObject(data.branchSaturdayEndHour),
                              branchSaturdayStartHour: this.$datetime.convertTimeStringToTimeObject(data.branchSaturdayStartHour ? data.branchSaturdayStartHour : ""),
                              branchWeekdayEndHour: this.$datetime.convertTimeStringToTimeObject(data.branchWeekdayEndHour),
                              branchWeekdayStartHour: this.$datetime.convertTimeStringToTimeObject(data.branchWeekdayStartHour),
                              attachInfoList: res.body.attachInfoList,
                              contentList: res.body.contentList,
                              statusCode: res.body.statusCode
                        }

                        this.modifyCard = {
                              registerTellerId: String(this.branchDate.firstRegisterTellerID),
                              registerTellerName: String(this.branchDate.firstRegisterTellerName),
                              registerDate: String(this.branchDate.firstRegisterDate),
                              registerTime: String(this.branchDate.firstRegisterTime),
                              updatedTellerId: this.branchInfo.statusCode === '09' ? "" : res?.body.lastChangeTellerID,
                              updatedTellerName: this.branchInfo.statusCode === '09' ? "" : res?.body.lastChangeTellerName,
                              updatedDate: this.branchInfo.statusCode === '09' ? "" : res?.body.lastChangeDate,
                              updatedTime: this.branchInfo.statusCode === '09' ? "" : res?.body.lastChangeTime,
                              deleteTellerId: this.branchInfo.statusCode === '09' ? res?.body.lastChangeTellerID : "",
                              deleteTellerName: this.branchInfo.statusCode === '09' ? res?.body.lastChangeTellerName : "",
                              deleteDate: this.branchInfo.statusCode === '09' ? res?.body.lastChangeDate : "",
                              deleteTime: this.branchInfo.statusCode === '09' ? res?.body.lastChangeTime : "",
                        };

                        this.requestFileDetail("02");
                  }
            },

            async requestFileDetail(attachTypeCode: string) {
                  for (let i = 0; i < this.branchInfo.attachInfoList.length; i++) {
                        if (this.branchInfo.attachInfoList[i].attachTypeCode == attachTypeCode) {
                              const res = await this.$file.getFileInfo(this.branchInfo.attachInfoList[i].fileId)
                              if (attachTypeCode == '02') {
                                    this.fileInfo.imageName = res.data.body.originalFileName
                                    this.fileInfo.imgSize = res.data.body.fileSizeString
                                    const resImage = await this.$file.previewImage(this.branchInfo.attachInfoList[i].fileId)
                                    this.fileInfo.image = resImage
                              }
                        }
                  }
            },

            isFileAlreadyExisted(fileContentType: FILE_TYPE.FILE | FILE_TYPE.IMAGE) {
                  const isFileAlreadyExisted = this.branchInfo.attachInfoList.some(obj => Object.values(obj).includes(fileContentType))
                  return isFileAlreadyExisted
            },

            mapValue(data: any) {
                  return {
                        branchSaturdayEndHour: this.keyCodeTime(data.branchSaturdayEndHour),
                        branchSaturdayStartHour: this.keyCodeTime(data.branchSaturdayStartHour),
                        branchWeekdayEndHour: this.keyCodeTime(data.branchWeekdayEndHour),
                        branchWeekdayStartHour: this.keyCodeTime(data.branchWeekdayStartHour),
                  }
            },

            handleClickAction(actionType: string) {
                  switch (actionType) {
                        case "delete":
                              this.$dialog.onOpen({
                                    content: this.$t("message.are_you_sure_you_want_to_delete"),
                                    dialogMode: "Question",
                                    callback: () => {
                                          this.handleDelete().then((res: boolean) => {
                                                if (res) {
                                                      this.handleListInfo();
                                                      modalController.dismiss();
                                                }
                                          });
                                          modalController.dismiss();
                                    },
                              });
                              break;
                        case "cancel":
                              this.handleCancel();
                              break;
                        case "edit":
                              this.isEdit = false;
                              break;
                        case "save":
                              this.validatFormRegister();
                              break;
                        default:
                              this.handleListInfo();
                              break;
                  }
            },

            onKeyDown(event: any) {
                  this.$validate.validateNumber(event)
            },

            checkSpace(event: Event) {
                  this.$validate.validateSpace(event)
            },

            validatFormRegister() {
                  if (
                        this.isInvalidLatitude ||
                        this.isInvalidLongitude ||
                        this.branchInfo.branchTel == "" ||
                        this.branchInfo.branchCode == "" ||
                        this.branchInfo.branchLatitude == "" ||
                        this.branchInfo.branchLongitude == "" ||
                        this.boardLangDefaultValue[0].branchName == "" ||
                        this.branchInfo.branchTel.length < 9
                  ) {
                        this.isInvalid = true;
                        this.isInvalidPhoneNumber = true
                  } else {
                        this.$dialog.onOpen({
                              content: `${this.$t('message.would_you_like_to_save')}`,
                              dialogMode: "Question",
                              callback: () => {
                                    if (this.viewType == 'edit') {
                                          this.handleEdit().then((res: boolean) => {
                                                if (res) {
                                                      this.inquiryBranchDetail();
                                                      this.isEdit = true
                                                      this.isInvalidPhoneNumber = false
                                                      modalController.dismiss()
                                                }
                                          });
                                    } else {
                                          this.handleRegister().then((res: boolean) => {
                                                if (res) {
                                                      this.handleListInfo();
                                                      modalController.dismiss()
                                                }
                                          });
                                    }
                              },
                        });
                  }

                  this.$util.scrollToElement('inp_error')
            },

            async handleRegister() {
                  if (this.getImage !== null) {
                        const res = await this.$file.uploadFile([this.getImage], "ICM");
                        for (let i = 0; i < res.data.body.fileList.length; i++) {
                              this.branchInfo.attachInfoList.push({
                                    filePath: res.data.body.fileList[i].fileContentType,
                                    attachTypeCode: FILE_TYPE.IMAGE,
                                    fileId: res.data.body.fileList[i].fileId
                              })
                        }
                  }

                  const reqBody: ICM21400121_REQ_BODY = {
                        branchCode: this.branchInfo.branchCode,
                        branchFax: this.branchInfo.branchFax,
                        branchLatitude: Number(this.branchInfo.branchLatitude),
                        branchLongitude: Number(this.branchInfo.branchLongitude),
                        branchSaturdayEndHour: this.convertTimeObjectToTimeString(this.branchInfo.branchSaturdayEndHour),
                        branchSaturdayStartHour: this.convertTimeObjectToTimeString(this.branchInfo.branchSaturdayStartHour),
                        branchWeekdayEndHour: this.convertTimeObjectToTimeString(this.branchInfo.branchWeekdayEndHour),
                        branchWeekdayStartHour: this.convertTimeObjectToTimeString(this.branchInfo.branchWeekdayStartHour),
                        branchTel: this.branchInfo.branchTel,
                        provinceCode: this.branchInfo.provinceCode,
                        displayYN: this.branchInfo.displayYN,
                        contentList: this.convertLangToCode(JSON.parse(JSON.stringify(this.boardLangDefaultValue))),
                        attachInfoList: this.branchInfo.attachInfoList
                  }

                  const res = await this.$request.getRequest('ICM21400121', reqBody, false) as ICM21400121_RES;

                  if (res.header.result) {
                        this.$dialog.onOpen({
                              content: this.$t("message.register_success"),
                              dialogMode: 'Success',
                              callback: () => {
                                    modalController.dismiss();
                              }
                        });
                  }

                  return res.header.result
            },

            async handleEdit() {
                  if (this.getImage !== null && !this.isImageEmty) {
                        if (this.branchInfo.attachInfoList.length === 0) {
                              const res = await this.$file.uploadFile([this.getImage], "ICM")
                              for (let i = 0; i < res.data.body.fileList.length; i++) {
                                    const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                                    this.branchInfo.attachInfoList.push({
                                          filePath: resFileInfo.data.body.filePath,
                                          attachTypeCode: FILE_TYPE.IMAGE,
                                          fileId: res.data.body.fileList[i].fileId
                                    })
                              }
                        } else if (this.branchInfo.attachInfoList.length > 0) {
                              if (this.isFileAlreadyExisted(FILE_TYPE.IMAGE)) {
                                    const res = await this.$file.uploadFile([this.getImage], "ICM")
                                    for (let index = 0; index < this.branchInfo.attachInfoList.length; index++) {
                                          if (this.branchInfo.attachInfoList[index].attachTypeCode === FILE_TYPE.IMAGE) {
                                                await this.$file.updateFileStatus(this.branchInfo.attachInfoList[index].fileId, FILE_TYPE.INACTIVE);
                                                for (let i = 0; i < res.data.body.fileList.length; i++) {
                                                      await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                                                            Object.assign(this.branchInfo.attachInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.IMAGE, filePath: v.data.body.filePath })
                                                      )
                                                }
                                          }
                                    }
                              } else {
                                    const res = await this.$file.uploadFile([this.getImage], "ICM")
                                    for (let i = 0; i < res.data.body.fileList.length; i++) {
                                          const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
                                          this.branchInfo.attachInfoList.push({
                                                filePath: resFileInfo.data.body.filePath,
                                                attachTypeCode: FILE_TYPE.IMAGE,
                                                fileId: res.data.body.fileList[i].fileId
                                          })
                                    }
                              }
                        }
                  }
                  if (this.getImage === null && this.isImageEmty) {
                        for (let i = 0; i < this.branchInfo.attachInfoList.length; i++) {
                              if (this.branchInfo.attachInfoList[i].attachTypeCode === FILE_TYPE.IMAGE) {
                                    await this.$file.updateFileStatus(this.branchInfo.attachInfoList[i].fileId, FILE_TYPE.INACTIVE)
                                    const index = this.branchInfo.attachInfoList.indexOf(this.branchInfo.attachInfoList[i]);
                                    this.branchInfo.attachInfoList.splice(index, 1);
                              }
                        }
                  }

                  const reqBody: ICM21400131_REQ_BODY = {
                        branchCode: this.branchInfo.branchCode,
                        branchFax: this.branchInfo.branchFax,
                        branchLatitude: Number(this.branchInfo.branchLatitude),
                        branchLongitude: Number(this.branchInfo.branchLongitude),
                        branchSaturdayEndHour: this.convertTimeObjectToTimeString(this.branchInfo.branchSaturdayEndHour),
                        branchSaturdayStartHour: this.convertTimeObjectToTimeString(this.branchInfo.branchSaturdayStartHour),
                        branchWeekdayEndHour: this.convertTimeObjectToTimeString(this.branchInfo.branchWeekdayEndHour),
                        branchWeekdayStartHour: this.convertTimeObjectToTimeString(this.branchInfo.branchWeekdayStartHour),
                        branchTel: this.branchInfo.branchTel,
                        provinceCode: this.branchInfo.provinceCode,
                        displayYN: this.branchInfo.displayYN,
                        contentList: this.convertLangToCode(JSON.parse(JSON.stringify(this.boardLangDefaultValue))),
                        attachInfoList: this.branchInfo.attachInfoList
                  }

                  const res = await this.$request.getRequest('ICM21400131', reqBody, false) as ICM21400131_RES;

                  if (res.header.result) {
                        this.$dialog.onOpen({
                              content: this.$t("message.update_success"),
                              dialogMode: 'Success',
                              callback: () => {
                                    this.inquiryBranchDetail()
                                    modalController.dismiss();
                              }
                        });
                  }

                  return res.header.result
            },

            handleCancel() {
                  this.$dialog.onOpen({
                        content: `${this.$route.query.type === 'edit' ? this.$t("message.cancel_without_saving_changes") : this.$t('message.are_you_sure_you_want_to_cancel_registration?')}`,
                        dialogMode: 'Question',
                        callback: () => {
                              if (this.viewType == "edit") {
                                    this.fileInfo.image = this.fileInfo.imageName = this.fileInfo.imgSize = ''
                                    this.inquiryBranchDetail();
                                    this.isEdit = true
                              } else {
                                    this.handleListInfo();
                              }
                        },
                  });
            },

            convertCommonCode(listArr: CONTENT_LIST[]): CONTENT_LIST[] {
                  const convertLangCode = listArr.map((item: CONTENT_LIST) => {
                        item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : "";
                        return item;
                  });
                  return convertLangCode;
            },

            convertLangToCode(listsArr: CONTENT_LIST[]): CONTENT_LIST[] {
                  const data = listsArr.map((item: CONTENT_LIST) => {
                        item.languageCode = this.$codeConverter.convertValue(
                              "LanguageCodeTranslate",
                              item.languageCode,
                              "value"
                        );
                        return item;
                  });

                  return data;
            },

            async handleDelete() {
                  const reqBody: ICM21400141_REQ_BODY = {
                        branchCode: String(this.$route.query.branchCode)
                  }

                  const res = await this.$request.getRequest('ICM21400141', reqBody, false) as ICM21400141_RES;


                  if (res.header.result) {
                        this.$dialog.onOpen({
                              content: this.$t("message.delete_success"),
                              dialogMode: 'Success',
                              callback: () => {
                                    modalController.dismiss()
                              }
                        });
                  }

                  return res.header.result
            },

            handleMap() {
                  window.open(`https://www.google.com/maps/search/?api=1&query=${this.branchInfo.branchLatitude},${this.branchInfo.branchLongitude}`);
            },

            handleListInfo() {
                  this.$router.push('/ICM214001SC00');
            },

            keyCodeTime(time: string) {
                  let [hours, minutes] = time.split(":");
                  let hoursKeyCode = hours;
                  let minutesKeyCode = minutes;
                  return hoursKeyCode + minutesKeyCode + '00000';
            },

            convertTimeObjectToTimeString(time: TimeObject): string {
                  return `${('0' + time.hours).slice(-2)}:${('0' + time.minutes).slice(-2)}`
            },

            handleUploadImage(event: any) {
                  this.isImageEmty = false
                  const accectFile = /(\.png)$/i;
                  const files = event.target.files;
                  const file = files[0];
                  if (!accectFile.exec(file.name)) {
                        this.$dialog.onOpen({
                              content: this.$t('message.only_png_file_can_be_inserted'),
                              dialogMode: "Warning",
                              callback: () => {
                                    modalController.dismiss();
                              },
                        });
                  } else if (file.size > 2097152) {
                        this.$dialog.onOpen({
                              content: this.$t('message.file_size_must_not_exceed_2MB'),
                              dialogMode: "Warning",
                              callback: () => {
                                    modalController.dismiss();
                              },
                        });
                  } else {
                        this.getImage = file;
                        const size = fileSize.countFileSize(file.size);
                        this.fileInfo.imgSize = size;
                        const reader = new FileReader();
                        reader.readAsDataURL(file);
                        this.fileInfo.imageName = file.name;
                        reader.onload = () => {
                              this.fileInfo.image = String(reader.result);
                        };
                  }

                  const duplicatedFile = document.getElementById('btn02 btn_upload') as HTMLInputElement;
                  duplicatedFile.value = ''
            },

            removeFile() {
                  this.getImage = null
                  this.isImageEmty = true
                  this.fileInfo.imageName = "";
                  this.fileInfo.imgSize = ""
            },

            validateLate(event: KeyboardEvent) {
                  const charCode = (event.which) ? event.which : event.keyCode;
                  if (
                        ((event.target as HTMLInputElement).value.length >= 11)
                        && charCode !== 8
                  ) {
                        return event.preventDefault()
                  }
            },

            validateLong(event: KeyboardEvent) {
                  const charCode = (event.which) ? event.which : event.keyCode;
                  if (((event.target as HTMLInputElement).value.length >= 12) && charCode !== 8) {
                        return event.preventDefault();
                  }
            },

            handleOnBlur() {
                  if (this.branchInfo.branchTel.length < 9) {
                        this.isInvalidPhoneNumber = true
                  } else {
                        this.isInvalidPhoneNumber = false
                  }
            },

            handleOnFocus() {
                  if (this.branchInfo.branchTel.length < 9) {
                        this.isInvalidPhoneNumber = false
                  } else {
                        this.isInvalidPhoneNumber = true
                  }
            },

            handleFaxNo(event: KeyboardEvent) {
                  const charCode = (event.which) ? event.which : event.keyCode;
                  if (((event.target as HTMLInputElement).value.length >= 12) && charCode !== 8) {
                        return event.preventDefault()
                  }
            },
      }

})
